.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  height: $top-bar-height;
  padding-top: 33px;
  box-sizing: border-box;
  color: $pure-white-color;

  a {
    display: block;
    height: ($top-bar-height - 16px);
    padding: 16px 16px 0;
    margin-top: -16px;
    color: $pure-white-color;
    cursor: pointer;

    &.menu-hamburger {
      float: left;
    }

    &.menu-dots {
      float: right;
    }

    &.back-arrow {
      float: left;
    }
  }

  .publish-date {
    float: right;
    margin-right: 16px;
    color: $primary-color;
    text-transform: none;
    font-weight: 200;
  }
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  width: 260px;
  height: 100%;
  overflow-y: scroll;
  background-color: $primary-color;
  color: $pure-white-color;
  -webkit-transform: translateX(-300px);
  -moz-transform: translateX(-300px);
  -o-transform: translateX(-300px);
  -ms-transform: translateX(-300px);
  transform: translateX(-300px);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}

ul.sidebar-menu {
  display: flex;
  flex-direction: column;
  margin-top: 115px;
  font-size: 12px;
  text-transform: uppercase;

  li {
    padding: 1.25rem 2rem;
    background-color: lighten($primary-color, 10%);
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);

    span {
      margin-left: 2rem;
    }
  }
}
