.contact-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  margin-bottom: 1rem;
  text-align: left;
  background-color: $pure-white-color;
}

.contact-text {
  font-size: 16px;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  flex-basis: 70%;

  .contact-description {
    font-size: 11px;
  }
}

.contact-follow {
  display: inline-block;
  padding: 0.5rem;
  margin: -1rem 0 1rem 0;
  border: 2px solid $secundary-color;
  background-color: lighten($secundary-color, 40%);
  font-size: 16px;
  color: $secundary-color;
  cursor: pointer;
}
