.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  height: 68px;
  padding-top: 33px;
  box-sizing: border-box;
  color: #fff;
}

.top-bar a {
  display: block;
  height: 52px;
  padding: 16px 16px 0;
  margin-top: -16px;
  color: #fff;
  cursor: pointer;
}

.top-bar a.menu-hamburger {
  float: left;
}

.top-bar a.menu-dots {
  float: right;
}

.top-bar a.back-arrow {
  float: left;
}

.top-bar .publish-date {
  float: right;
  margin-right: 16px;
  color: #ff6b6c;
  text-transform: none;
  font-weight: 200;
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  width: 260px;
  height: 100%;
  overflow-y: scroll;
  background-color: #ff6b6c;
  color: #fff;
  -webkit-transform: translateX(-300px);
  -moz-transform: translateX(-300px);
  -o-transform: translateX(-300px);
  -ms-transform: translateX(-300px);
  transform: translateX(-300px);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}

ul.sidebar-menu {
  display: flex;
  flex-direction: column;
  margin-top: 115px;
  font-size: 12px;
  text-transform: uppercase;
}

ul.sidebar-menu li {
  padding: 1.25rem 2rem;
  background-color: #ff9e9f;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

ul.sidebar-menu li span {
  margin-left: 2rem;
}

