.btn {
  display: block;
  width: 100%;
  padding: 1rem;
  margin-bottom: 1rem;
  border: 2px solid;
  color: #fff;
  text-transform: uppercase;
  font-family: inherit;
  font-weight: 600;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
}

.btn:focus {
  border: 2px solid;
  outline: none;
}

.btn.primary-btn {
  background-color: #ff6b6c;
  border-color: #ff6b6c;
}

.btn.primary-btn:hover {
  background-color: #ffd1d1;
}

.btn.primary-btn.disabled {
  background-color: #ffd1d1 !important;
  border-color: #ff9e9f !important;
  color: rgba(255, 255, 255, 0.5) !important;
  cursor: not-allowed !important;
}

.btn.secundary-btn {
  background-color: #2598c8;
  border-color: #2598c8;
}

.btn.secundary-btn:hover {
  background-color: #6fc2e4;
}

.btn.secundary-btn.disabled {
  background-color: #6fc2e4 !important;
  border-color: #44afdc !important;
  color: rgba(255, 255, 255, 0.5) !important;
  cursor: not-allowed !important;
}

.btn.succes-btn {
  background-color: #1ab768;
  border-color: #1ab768;
}

.btn.succes-btn:hover {
  background-color: #51e69b;
}

.btn.succes-btn.disabled {
  background-color: #51e69b !important;
  border-color: #24e081 !important;
  color: rgba(255, 255, 255, 0.5) !important;
  cursor: not-allowed !important;
}

.btn.follow-btn {
  color: #2598c8;
  background-color: #fff;
  border: 1px solid;
  border-color: #2598c8;
  border-radius: 3px;
  width: 85px;
  height: 30px;
  position: absolute;
  top: 50px;
  left: 50px;
  cursor: hand;
}

