ul.tag-list {
  display: flex;
  font-weight: 300;

  li {
    display: inline-block;
    padding: 0.7rem;
    margin-right: 10px;
    border: 2px solid lighten($primary-color, 10%);
    color: $pure-white-color;
    background-color: lighten($primary-color, 20%);

    @include border-radius(6px);
  }
}
