.notification {
  padding: 1rem;
  color: $pure-white-color;
  margin-bottom: 1rem;

  p {
    padding: 0;
    margin: 0;
  }

  h2 {
    padding: 0;
    margin: 0;

    @include type-setting(-1);
  }

  &.succes {
    background-color: $succes-color;
    color: $pure-white-color;
  }

  &.error {
    background-color: $error-color;
  }

  &.alert {
    background-color: $favorites-color;
  }
}
