@mixin type-setting($level: 0) {
  font-size: modular-scale($level);
  //line-height: line-height($level);
}

/// Align text vertically in container
/// @group abstracts
/// @access public
@mixin vertical-center-text {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

@mixin border-radius($value) {
  border-radius: $value;
  -webkit-border-radius: $value;
  -moz-border-radius: $value;
}

@mixin drop-shadow($h-offset, $v-offset, $blur, $spread, $color) {
  box-shadow: $h-offset $v-offset $blur $spread $color;
  -webkit-box-shadow: $h-offset $v-offset $blur $spread $color;
  -moz-box-shadow: $h-offset $v-offset $blur $spread $color;
}

@mixin disabled($color) {
  &.disabled {
    background-color: lighten($color, 20%) !important;
    border-color: lighten($color, 10%) !important;
    color: rgba(255, 255, 255, 0.5) !important;
    cursor: not-allowed !important;
  }
}
