.blog-post-preview {
  padding: 2rem;
  margin-bottom: 1rem;
  background-color: $pure-white-color;
}

.project-preview {
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: $pure-white-color;
  border: solid 5px $pure-white-color;

  h2 {
    margin-top: 0;
  }

  .preview-images {
    display: flex;
    justify-content: space-between;
    margin: 1rem -2rem;
    padding: 5px;
    background-color: $base-color-5;

    .image-1 {
      flex: 0 0 59%;
      height: 250px;
      background-color: $base-color-4;
      background-size: cover;
      background-position: center;
    }

    .image-2-3 {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      flex: 0 0 40%;
      height: 250px;
    }

    .image-2,
    .image-3 {
      flex: 0 0 49%;
      background-color: $base-color-4;
      background-size: cover;
      background-position: center;
    }
  }

  .project-footer {
    display: flex;
    justify-content: space-between;
  }
}
