/// Primary color
/// @group colors
/// @access public
/// @type Color
$primary-color: #ff6b6c;

/// Secundary color
/// @group colors
/// @access public
/// @type Color
$secundary-color: #2598c8;

/// Succes color
/// @group colors
/// @access public
/// @type Color
$succes-color: #1ab768;

/// Color mainly for 'likes' and errors
/// @group colors
/// @access public
/// @type Color
$error-color: #e34549;

/// Color mainly for 'add to favorites'
/// @group colors
/// @access public
/// @type Color
$favorites-color: #f6b63b;

/// White
/// @group colors
/// @access public
/// @type Color
$pure-white-color: #fff;

/// Dark base color
/// @group colors
/// @access public
/// @type Color
$base-color-0: #373737;

/// Base color 15% lighter
/// @group colors
/// @access public
/// @type Color
$base-color-1: lighten($base-color-0, 15%);

/// Base color 30% lighter
/// @group colors
/// @access public
/// @type Color
$base-color-2: lighten($base-color-0, 30%);

/// Base color 45% lighter
/// @group colors
/// @access public
/// @type Color
$base-color-3: lighten($base-color-0, 45%);

/// Base color 60% lighter
/// @group colors
/// @access public
/// @type Color
$base-color-4: lighten($base-color-0, 60%);

/// Base color 75% lighter
/// @group colors
/// @access public
/// @type Color
$base-color-5: lighten($base-color-0, 75%);
