.cover-post {
  width: 100%;
  height: 280px;
  padding: $grid-gutter-width;
  margin-top: (-101px + $top-bar-height);
  background: url('http://via.placeholder.com/400x300') no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  h3 {
    margin-top: 0;
    float: left;
  }

  span.date {
    float: right;
    font-weight: 200;
  }

  h2 {
    margin-bottom: 0;
    color: $pure-white-color;
  }
}
