.btn {
  display: block;
  width: 100%;
  padding: 1rem;
  margin-bottom: 1rem;
  border: 2px solid;
  color: $pure-white-color;
  text-transform: uppercase;
  font-family: inherit;
  font-weight: 600;

  @include border-radius(4px);

  &:focus {
    border: 2px solid;
    outline: none;
  }

  &.primary-btn {
    background-color: $primary-color;
    border-color: $primary-color;

    &:hover {
      background-color: lighten($primary-color, 20%);
    }

    @include disabled($primary-color);
  }

  &.secundary-btn {
    background-color: $secundary-color;
    border-color: $secundary-color;

    &:hover {
      background-color: lighten($secundary-color, 20%);
    }

    @include disabled($secundary-color);
  }

  &.succes-btn {
    background-color: $succes-color;
    border-color: $succes-color;

    &:hover {
      background-color: lighten($succes-color, 20%);
    }

    @include disabled($succes-color);
  }

  &.follow-btn {
    color: $secundary-color;
    background-color: $pure-white-color;
    border: 1px solid;
    border-color: $secundary-color;
    border-radius: 3px;
    width: 85px;
    height: 30px;
    position: absolute;
    top: 50px;
    left: 50px;
    cursor: hand;
  }
}
