.form-group {
  box-sizing: border-box;
  background-color: $pure-white-color;
  padding: 2rem;
  margin-bottom: 1rem;
  text-align: center;
}

.auth-form {
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: relative;
  background: none;

  input[type='submit'] {
    position: absolute;
    max-width: 600px;
    bottom: 0;
    left: 0;
  }

  .flex-item {
    flex-basis: 75%;
  }
}

.form-control {
  display: block;
  width: 100%;
  height: 48px;
  padding: 6px 15px;
  margin-bottom: 1rem;
  color: $base-color-1;
  background-color: $pure-white-color;
  border: 1px solid $base-color-3;
  font-family: inherit;
  font-size: inherit;

  &:focus {
    border-color: $secundary-color;
    outline: none;
  }

  &.succes {
    border-color: $succes-color;
    color: $succes-color;
    font-weight: 600;
  }

  &.error {
    border-color: $error-color;
    color: $error-color;
    font-weight: 600;
  }
}

.textarea {
  height: 10rem;
}

.hide {
  display: none;
}
