.color-swatch {
  height: 90px;
  margin-right: 20px;
  margin-bottom: 20px;
  text-align: center;
  color: $pure-white-color;
  flex: 0 0 90px;

  @include vertical-center-text;

  &.primary {
    background-color: $primary-color;
  }

  &.secundary {
    background-color: $secundary-color;
  }

  &.succes {
    background-color: $succes-color;
  }

  &.error {
    background-color: $error-color;
  }

  &.favorites {
    background-color: $favorites-color;
  }
}

.example {
  position: relative;
  display: inline-block;
  border: 1px solid $base-color-4;
  padding: 30px 15px 15px;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    padding: 0;
  }

  &::before {
    position: absolute;
    top: 15px;
    left: 15px;
    text-transform: uppercase;
    font-size: 11px;
    color: $base-color-3;
    content: 'Example';
  }
}
