@for $i from 0 through 6 {
  .base-color-#{$i} {
    background-color: lighten($base-color-0, $i*15);
  }
}

.no-bg {
  background: none;
}

.white-bg {
  background-color: $pure-white-color;
}

.primary {
  color: $primary-color;
}

.primary-bg {
  background-color: $primary-color;
}

.secundary {
  color: $secundary-color;
}

.secundary-bg {
  background-color: $secundary-color;
}

.error {
  color: $error-color;
}

.favorites {
  color: $favorites-color;
}
